import React, {useState} from 'react'
import {ActionButton} from './buttons'

function getLastNumber(url) {
  var matches = url.match(/\d+/g);
  return matches[matches.length - 1];
}

export function Post(props){
  const {post, className}=props
  const [actionPost, setActionPost]=useState(props.post ? props.post :null)

  const url=window.location.href
  const idFromUrl=getLastNumber(url)
  const alreadyInDetail= post.id === parseInt(idFromUrl)
  
  const handlePerformAction=(newActionPost)=>{
    setActionPost(newActionPost)
  }

  const handlePostDetailClick=(event)=>{

    event.preventDefault()
    window.location.href=`/${post.id}`

  }


    return <div className={className}>
  
      <p>#{post.id} - {post.content}</p>
        <ActionButton className='btn btn-secondary' post={actionPost} didPerformAction={handlePerformAction} action={{type: "like", display:"Likes"}}></ActionButton>
        <ActionButton className='btn btn-secondary' post={actionPost} didPerformAction={handlePerformAction}  action={{type: "dislike", display:"Dislike"}}></ActionButton>
       {alreadyInDetail===true?null: <button className='btn btn-secondary' onClick={handlePostDetailClick}>DETAIL</button>}
    </div>
  }
  



  